.bar{
   
    margin-left: 0px;
    height: 3px;
    border:2px solid #ff4957;
    background-color: white;
    margin-bottom: 10px;
    border-radius: 20px;
}
.bar1::before{
    
    content: "";
    border-radius: 50%;
    border: rgb(255, 255, 255);
    display: inline-block;
    padding: 2px;
    /* width: 70%; */
    animation-name: tabi;
    animation-duration: 1.5s;
    /* animation-iteration-count: infinite; */
    animation-fill-mode: forwards;
    height: 2px;
    background-color: white;
}
@keyframes tabi {
    from{
        width: 1%;
    }
to{
    width: 75%;
}
}
.shadowspan{
    width: 50%;
    display: block;
    overflow: hidden;
    background-color: cadetblue;
    border: #fff;
    border-radius: 20px;
